import { all, fork } from 'redux-saga/effects';
import axios from 'axios';
import postSaga from './post';
import userSaga from './user';
axios.defaults.baseURL = 'http://localhost:3065'; //sagas  post url 

axios.defaults.withCredentials = true; //    saga  .

export default function* rootSaga() {
  yield all([//all   
  fork(postSaga), fork(userSaga) // fork    
  //        
  // call    
  //     . 
  ]);
} // generate  function*
